import { ProductCard } from "../../../../components";
import SingleProduct from "./SingleProduct";

const products = ['product-one', 'product-two', 'product-one', 'product-two', 'product-three', 'product-four', 'product-five', 'product-six'];

const renderMnueProdcusts = products.map((product, index) => {
    return <div className='mb-5'><SingleProduct key={index} imageName={product}></SingleProduct></div>
});

const renderGridProducts = [...products, ...products, ...products].map((product, index) => {
    return <ProductCard hasDiscount={index % 2 === 0} hasOffer={index % 2 !== 0} key={index} classNames={'col-span-12 sm:col-span-6 xl:col-span-4'} imageName={product}></ProductCard>
});


export {
    renderMnueProdcusts,
    renderGridProducts
}